import React from "react"
import { Typography } from "@mui/material"
import { Container, Grid, Stakeholder, Icon } from "./styled"

interface stakeholderProps {
  stakeholders?: Array<{
    img?: string
    title?: string
  }>
  content?: string
}

const Component: React.FC<stakeholderProps> = ({
  stakeholders = [],
  content = ""
}) => {
  return (
    <Container>
      <Typography variant="h1CursiveBold">Stakeholders</Typography>
      <Typography variant="h4Bold">{content}</Typography>
      <Grid>
        {stakeholders.map((stakeholder, index) => (
          <Stakeholder>
            <Icon src={stakeholder.img} alt="Children" />
            <Typography key={index} variant="bodyTextMedium">
              {stakeholder.title}
            </Typography>
          </Stakeholder>
        ))}
      </Grid>
    </Container>
  )
}

export default Component
