import React from "react"
import { Typography } from "@mui/material"
import {
  OutcomeContainer,
  Outcome,
  TextSection,
  OutcomeList,
  SubOutcomeList
} from "./styled"
import { BasicCarousel } from "@components/Carousel"

interface ComponentProps {
  title?: string
  outcomes?: Array<{
    id?: number
    content?: string
    subcontent?: string[]
  }>
}

const Component: React.FC<ComponentProps> = ({ title = "", outcomes = [] }) => {
  return (
    <OutcomeContainer>
      <Outcome>
        <div className="container slide-container">
          <BasicCarousel />
        </div>
        <div className="container">
          <TextSection>
            {title && <Typography variant="h1CursiveBold">{title}</Typography>}
            <OutcomeList>
              {outcomes.map((outcome, index) => (
                <>
                  <Typography variant="smallBody">
                    {index + 1}. {outcome.content}
                  </Typography>
                  {outcome.subcontent && (
                    <SubOutcomeList>
                      {outcome.subcontent.map((sub, subIndex) => (
                        <SubOutcomeList>
                          <Typography variant="smallBody">
                            {subIndex + 1}. {sub}
                          </Typography>
                        </SubOutcomeList>
                      ))}
                    </SubOutcomeList>
                  )}
                </>
              ))}
            </OutcomeList>
          </TextSection>
        </div>
      </Outcome>
    </OutcomeContainer>
  )
}

export default Component
